<template>
  <div>
  <!--<md-card>-->
    <md-card-header>
      <div class="md-title" style="text-align: left">
        股票列表
      </div>
      <p class="md-subhead">
        是否有心仪的股票呢
      </p>
    </md-card-header>
    <md-card-content class="md-card-content">
      <md-content style="overflow-y: hidden; overflow-x:scroll; white-space:nowrap; width: 100%"
                  class="md-scrollbar">
        <template v-for="stock in this.stocks">
          <md-card class="scrollable-card">
            <md-card-header>
              <md-card-header-text>
                <h3 class="nobr" style="max-width: 150px;">{{ stock.name }}</h3>
                <p class="md-subhead">{{ stock.stockId }}</p>
                <p class="md-subhead">开盘价：{{ stock.openingPrice }}元</p>
                <p class="md-subhead">当前价：{{ stock.stockPrice }}元</p>
              </md-card-header-text>
            </md-card-header>
          </md-card>
        </template>
      </md-content>
    </md-card-content>
  <!--</md-card>-->
  </div>
</template>

<script>
  export default {
    props: ['stocks'],
    name: 'StocksCard'
  }
</script>

<style scoped>
  .scrollable-card {
    display: inline-block;
    width: 250px;
    position: relative;
    margin-bottom: 20px !important;
    margin-right: 40px;
  }

  .nobr {
    display: block;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .stock-logo{
    max-width: 100%;
    max-height: 100%;
    /*margin-left: 20px;*/
  }
</style>



